.search-form {
  height: 60px;
  background-color: #007bff;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 20%;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border-color: #fff;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  display: grid;
}

.photo-card {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  transition: transform .3s;
}

.photo-card:hover {
  transform: scale(1.05);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.info {
  justify-content: space-between;
  margin-top: 10px;
  display: flex;
}

.info-item {
  font-size: 14px;
}

